import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { Util } from '../util'

interface SliceState {
  gettingItem: boolean
  item?: any
  collection?: any
  propose?: any
  importing: boolean
  tag?: any
}

const item = createSlice({
  name: 'item',
  initialState: { gettingItem: false, item: {}, collection: {} } as SliceState,
  reducers: {
    itemRequest(state, action: PayloadAction<any>) {
      state.gettingItem = true
    },
    itemSuccess: {
      reducer(state, action: PayloadAction<any>) {
        const { data } = action.payload
        data.collection.Status = Util.getStatusListFromCollection(data.collection)
        state.gettingItem = false
        state.item = data.item
        state.collection = data.collection
      },
      prepare(data) {
        return { payload: { data } }
      }
    },
    itemFailure(state, action: PayloadAction<any>) {      
      state.gettingItem = false
    },
    itemUpdate: {
      reducer(state, action: PayloadAction<any>) {
        if (!state.collection) {
          return
        }
        const { type, value } = action.payload
        Util.updateCollection(state.collection, type, value)
      },
      prepare(type: string, value: string) {
        return { payload: { type, value } }
      }
    },
    setImportState: {
      reducer(state, action: PayloadAction<boolean>) {
        state.importing = action.payload
      },
      prepare(value: boolean) {
        return { payload: value }
      }
    },
    itemProposeKorean: {
      reducer(state, action: PayloadAction<any>) {
        state.propose = {
          type: action.payload.type,
          value: action.payload.name,
          id: action.payload.id,
          path: action.payload.path,
        }
      },
      prepare(type: string, id: number, name: string, path: string) {
        return { payload: { type, id, name, path } }
      }
    },
    tagSuccess: {
      reducer(state, action: PayloadAction<any>) {
        state.tag = action.payload.data
      },
      prepare(data:any) {
        return { payload: { data } }
      }
    }
  }
})

export const {
  itemRequest, itemSuccess, itemFailure, itemUpdate,
  setImportState, itemProposeKorean, tagSuccess,
} = item.actions
export default item.reducer