import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { Util } from '../util'

interface SliceState {
  showModal: boolean
  collection: any
}

const modal = createSlice({
  name: 'modal',
  initialState: { showModal: false, collection: {} } as SliceState,
  reducers: {
    showModal: {
      reducer(state, action: PayloadAction<any>) {
        state.showModal = true
        state.collection = action.payload
      },
      prepare(collection: any) {
        return { payload: { collection } }
      }
    },
    hideModal(state, action: PayloadAction<any>) {
      state.showModal = false
    },
    updateModal: {
      reducer(state, action: PayloadAction<any>) {
        const { type, value } = action.payload
        Util.updateCollection(state.collection, type, value)
      },
      prepare(type: string, value: string) {
        return { payload: { type, value } }
      }
    }
  }
})

export const { showModal, hideModal, updateModal } = modal.actions
export default modal.reducer